import './OportunidadTransportista.css'

import { IonSpinner, useIonViewDidEnter } from '@ionic/react'
import { LatLngLiteral } from 'leaflet'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { MapContainer, Marker, Polyline, TileLayer } from 'react-leaflet'
import { RouteComponentProps } from 'react-router'

import { PageApp } from '../../components'
import { RUTAS_APP, TIPO_ITINERARIO } from '../../constants'
import {
  ciudades_to_str,
  distancia_km_fmt,
  esRuta,
  fecha_fmt,
  minutos_to_str,
  plata_fmt,
  string2leaflet_location,
} from '../../helpers'
import {
  centroItinerarios,
  ICONOS_GLOBO,
  itinerario_marker,
  itinerario_popup,
  useApiToaster,
  useConfirm,
  useToaster,
} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { GenericResponse, HojaRutaApi, ItinerarioApi, Vehiculo } from '../../services/api'

// @ts-ignore
export const OportunidadTransportista: React.FC<RouteComponentProps> = observer(({ history, location }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Ready, setReady] = useState(false)
  const [Loading, setLoading] = useState(false)
  const [Location, setLocation] = useState<LatLngLiteral | null>(null)
  const [Center, setCenter] = useState<LatLngLiteral | null>(null)
  const [Polylines, setPolyline] = useState<LatLngLiteral[] | null>()
  const [Ruta, setRuta] = useState<HojaRutaApi | null>()
  const [Vehiculo, setVehiculo] = useState<Vehiculo | null>()

  const apiToaster = useApiToaster()
  const addToast = useToaster()

  const setup = async () => {
    setRuta(transportistaStore.oportunidadDetalle)
  }

  useEffect(() => {
    setup()
  }, [])

  useEffect(() => {
    setRuta(transportistaStore.oportunidadDetalle)
  }, [transportistaStore.oportunidadDetalle])

  useEffect(() => {
    setVehiculo(transportistaStore.vehiculoSeleccionado as any)
  }, [transportistaStore.vehiculoSeleccionado])

  useEffect(() => {
    setLocation(transportistaStore.lastLoc)

    if (!Ruta) return

    let convertidos = Ruta.itinerarios.map((i: ItinerarioApi) => string2leaflet_location(i.ubicacion))

    if (transportistaStore.lastLoc) {
      convertidos.splice(0, 0, transportistaStore.lastLoc)
    }

    setCenter(centroItinerarios(convertidos))
    setPolyline(convertidos)
    setTimeout(() => {
      setReady(true)
    }, 700)
  }, [Ruta, transportistaStore.lastLoc])

  const esOportunidad = () => {
    // TODO set estado para saber si es ruta u oportunidad, no anda bien por ruta en la transicion
    return esRuta(location, RUTAS_APP.OPORTUNIDAD)
  }

  const vehiculoEsValido = () => {
    return esOportunidad() || (Ruta && Vehiculo)
  }

  const [mostrarDialogo, ConfirmDialog] = useConfirm(
    () => {
      if (!Ruta) return
      setLoading(true)
      rootStore.environment.api.aceptarRuta(Ruta.id).then((result) => {
        setLoading(false)
        apiToaster(result, 'Oportunidad aceptada', 'aceptar la oportunidad')
        if (result.kind === 'ok') {
          history.replace(RUTAS_APP.MIS_RUTAS)
        }
      })
    },
    () => {},
    '',
    '',
    true,
    '#ff6f33',
    'btn-outline-orange',
  )

  const aceptarOportunidad = () => {
    if (!Ruta) return
    mostrarDialogo('Confirma que desea aceptar la oportunidad?', null)
  }

  const [mostrarDialogo2, ConfirmDialog2] = useConfirm(
    () => {
      if (!Ruta) return
      if (!Vehiculo) return
      setLoading(true)
      rootStore.environment.api.empezarRuta(Ruta, Vehiculo).then((result: GenericResponse) => {
        setLoading(false)
        apiToaster(result, 'Ruta iniciada!', 'empezar la ruta')
        if (result.kind === 'ok') {
          // @ts-ignore
          history.push({ pathname: RUTAS_APP.HOME, cerrarMenu: true })
        }
      })
    },
    () => {},
    '',
    '',
    true,
    '#ff6f33',
    'btn-outline-orange',
  )

  const empezarRuta = () => {
    if (!Ruta) return
    if (!Vehiculo) {
      addToast('Debe seleccionar un vehículo para iniciar la ruta', 'warning')
      return
    }
    mostrarDialogo2('Confirma que desea comenzar la ruta?', null)
  }

  const seleccionarVehiculo = () => () => {
    // TODO filter vehiculo
    history.push(RUTAS_APP.VEHICULOS)
  }

  if (!Ruta) {
    return null
  }

  return (
    <PageApp
      pageStyles={'transportista-page background-blanco'}
      titulo_header={esOportunidad() ? 'Oportunidad # ' + Ruta.id : 'Ruta # ' + Ruta.id}
      accion_boton_derecho={() => history.goBack()}
      boton_derecho={<FIcon.X size={30} color={'white'} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      {ConfirmDialog}
      {ConfirmDialog2}
      <div className={'oportunidad-list-container py-2'}>
        <div style={{ height: '40%', width: '100%' }}>
          {Center && Ready && (
            <MapContainer
              center={Center}
              zoom={13}
              style={{ height: '100%', width: '100%' }}
              zoomControl={false}
              attributionControl={false}
              scrollWheelZoom={false}
              key={Ruta.id}
            >
              <TileLayer
                attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.osm.org/{z}/{x}/{y}.png"
              />
              {Polylines && <Polyline positions={Polylines} />}
              {Ruta &&
                Ruta.itinerarios.map((itinerario: ItinerarioApi) =>
                  itinerario_marker(itinerario, itinerario_popup(itinerario)),
                )}
              {Location && <Marker position={Location} icon={ICONOS_GLOBO.transportista2} />}
            </MapContainer>
          )}
        </div>

        <div className={'item-row'}>
          <span>Inicio</span>
          <span>{fecha_fmt(Ruta.inicio.fecha_hora_calculada_desde)}</span>
        </div>
        <div className={'item-row'}>
          <span>Fin</span>
          <span>{fecha_fmt(Ruta.fin.fecha_hora_calculada_desde)}</span>
        </div>
        <div className={'item-row item-ciudades'}>
          <span>{ciudades_to_str(Ruta.ciudades)}</span>
        </div>
        <div className={'item-row '}>
          <div className={'item-row-group'}>
            <FIcon.Clock size={20} />
            <span>{minutos_to_str(Ruta.minutos_trabajo)}</span>
          </div>

          <div className={'item-row-group'}>
            <FIcon.Navigation size={20} />
            <span>{distancia_km_fmt(Ruta.kilometros_recorrido)}</span>
          </div>

          <div className={'item-row-group'}>
            <FIcon.GitBranch size={20} />
            <span>{Ruta.cantidad_paradas}</span>
          </div>
        </div>
        <div className={'item-row item-ciudades'}>
          <span>
            <FIcon.DollarSign size={20} />
            {plata_fmt(Ruta.tarifa_transportista)}
          </span>
        </div>
        <div className={'item-itinerarios'}>
          <span>Itinerario</span>
          <div>
            {Ruta.itinerarios.map((itinerario: ItinerarioApi) => {
              return (
                <div key={`itinerario-${itinerario.id}`} className={'item-itinerario'}>
                  <div>
                    <span>
                      <strong>
                        {itinerario.orden} - {itinerario.tipo === TIPO_ITINERARIO.ENTREGA ? 'Entrega' : 'Retiro'} •{' '}
                        {itinerario.ciudad.nombre}
                      </strong>
                    </span>
                  </div>
                  <div>
                    <span>{itinerario.direccion}</span>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
        {!Ruta.finalizado && transportistaStore.itinerarioActivo === null && (
          <>
            {!esOportunidad() && Vehiculo && (
              <div className={'item-row item-vehiculo my-4'} onClick={seleccionarVehiculo()}>
                <div className={`item-row-group btn ${vehiculoEsValido() ? 'btn-success' : 'btn-warning'}`}>
                  <FIcon.Truck size={20} />
                  <span>Vehiculo:</span>
                  <span>{Vehiculo.modelo + ' ' + Vehiculo.numero_chapa}</span>
                </div>
              </div>
            )}
            {!esOportunidad() && !Vehiculo && (
              <div className={'item-row item-vehiculo my-4'} onClick={seleccionarVehiculo()}>
                <div className={`item-row-group btn ${vehiculoEsValido() ? 'btn-success' : 'btn-warning'}`}>
                  <FIcon.Truck size={20} />
                  <span>Vehiculo:</span>
                  <span>Seleccionar</span>
                </div>
              </div>
            )}
            <div className={'item-row mb-3'}>
              <button
                className={'btn btn-outline-orange btn-block'}
                disabled={Loading}
                onClick={() => {
                  esOportunidad() ? aceptarOportunidad() : empezarRuta()
                }}
              >
                {Loading && <IonSpinner name="crescent" />}
                {!Loading && <span>{esOportunidad() ? 'Aceptar' : 'Empezar'} Ruta</span>}
              </button>
            </div>
          </>
        )}
      </div>
    </PageApp>
  )
})

export default OportunidadTransportista
